import React, { Component } from 'react';
import {connect} from 'react-redux';
import {shuffle} from 'lodash'

import { Container,HomeBanner,Box,Row, Button,Col,Image, Column } from '../../styles/init';
import Form from '../../reuseables/form';
import Navigation from '../../reuseables/navigation';
import Footer from '../../reuseables/footer';
import {ViewAll} from '../../redux/actions/viewAllCars';
import Spinner from '../../reuseables/spinner';


class Home extends Component {
    constructor(props) {
      super(props);
      this.props.ViewAll()
      this.state = {
        "box_arrow": "arrow_right",
        searchData: {
          location: "",
          lowest: "",
          highest: "",
          model: "",
          type: "",
        }
      }
    }

    onBoxhover=()=>{ this.setState({ "box_arrow": "arrow_right_white" }) };
    onBoxOut=()=>{ this.setState({ "box_arrow": "arrow_right" }) };

    quickSearch = () => {

        const queryParams = [];
        for(let i in this.state.searchData) {
          queryParams.push(encodeURIComponent(i) + '=' + encodeURIComponent(this.state.searchData[i]))
        }

        const queryString = queryParams.join('&');
        this.props.history.push({
          pathname: "/cars",
          search: '?' + queryString
        })
    }

    getLocationData = (value,valid) => { 
      this.setState({
              ...this.state,
              searchData: {
                ...this.state.searchData,
                location : value
              }
      })
    }

    getHighestData = (value,valid) => { 
      this.setState({
              ...this.state,
              searchData: {
                ...this.state.searchData,
                highest : value
              }
      })
    }

    getModelData = (value,valid) => { 
      this.setState({
              ...this.state,
              searchData: {
                ...this.state.searchData,
                model : value
              }
      })
    }

    getLowestData = (value,valid) => { 
      this.setState({
              ...this.state,
              searchData: {
                ...this.state.searchData,
                lowest : value
              }
      })
    }

    getTypeData = (value,valid) => { 
      this.setState({
              ...this.state,
              searchData: {
                ...this.state.searchData,
                type : value
              }
      })
    }

    render() {
      const{loading, payload, success} = this.props;
      let latestCars = [];
      let content = <Row justify="center">
                      <Spinner />
                    </Row>
      if(success) {
        Object.keys(payload.cars).map((car)=>{
          latestCars.push(payload.cars[car])
        })
        content = shuffle(latestCars).splice(0,4).map((car, index) => {
          return (
            <Box width="23%" height="275px" padding="10px 10px 15px 10px" left="1%" right="1%" key={index}>
              <Column height="100%">
                <Row height="151px" bottomspacing="10px">
                  <Image src={require('../../assets/car.png')} alt="lady_car"/>
                </Row>

                <Row bottomspacing="10px">
                  <h3>{car.make + " " + car.series}</h3>
                </Row>

                <Row  bottomspacing="10px">
                  <p>
                    is simply dummy text of the printing and typesetting industry. Lorem……
                  </p>   
                </Row>
              </Column>                    
            </Box>
          )
        })

      }

        return (
          <Container>
            <Navigation/>
            <HomeBanner>
                <Box width="364px" height="450px" padding="26px 19px">
                  <Row bottomspacing="21px">
                      <Form
                          width="100%"
                          placeholder="Select a Location"
                          label="Location"
                          type="select"
                          options={["New York"]}
                          data={this.getLocationData}
                      />
                  </Row>
                  <Row bottomspacing="21px" justify="space-between">
                      <Form
                          width="45%"
                          placeholder="Lowest"
                          label="Price Range"
                          type="select"
                          options={["500"]}
                          data={this.getLowestData}
                      />
                      <Form
                          width="45%"
                          placeholder="Highest"
                          label=""
                          type="select"
                          options={["10000"]}
                          data={this.getHighestData}
                      />
                  </Row>
                  <Row bottomspacing="30px" justify="space-between">
                      <Form
                          width="45%"
                          placeholder="Select a Model"
                          label="Model"
                          type="select"
                          options={["Toyota"]}
                          data={this.getModelData}
                      />
                      <Form
                          width="45%"
                          placeholder="Select a Type"
                          label="Type"
                          type="select"
                          options={["SUV"]}
                          data={this.getTypeData}
                      />
                  </Row>
                  <Row>
                    <Button width="100%" onClick={this.quickSearch} height="58px">Search 500,000 cars</Button>
                  </Row>
                </Box>
            </HomeBanner>
            <Row padding="0 91px" justify="space-between" bottomspacing="48px">
              <Box width="30%" height="387px" padding="31px 34px" className="box_active box_active--selected" onMouseOver={this.onBoxhover} onMouseLeave={this.onBoxOut}>
                <Row bottomspacing="41px">
                  <img className="img_right_space" src={require('../../assets/search_box.svg')} alt="search"/>
                  <p >FIND LOCAL DEALER</p>
                </Row>
                <Row bottomspacing="30px">
                  <p className="paragraph_spacing">is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's </p>
                </Row>
                <Row justify="flex-end">
                  <p className="img_right_space linkdefault"  onClick={() => this.props.history.push('/dealer')}>Find Now</p>
                  <img src={require(`../../assets/${this.state.box_arrow}.svg`)} alt="arrow_right"/>
                </Row>
              </Box>
              <Box width="30%" height="387px" padding="31px 34px" className="box_active" onMouseOver={this.onBoxhover} onMouseLeave={this.onBoxOut}>
                <Row bottomspacing="41px">
                  <img className="img_right_space" src={require('../../assets/dollar_box.svg')} alt="search"/>
                  <p>SELL YOUR CAR</p>
                </Row>
                <Row bottomspacing="30px">
                  <p className="paragraph_spacing">is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's </p>
                </Row>
                <Row justify="flex-end">
                  <p className="img_right_space linkdefault" onClick={() => this.props.history.push('/sell')} >Sell Now</p>
                  <img src={require(`../../assets/${this.state.box_arrow}.svg`)} alt="arrow_right"/>
                </Row>
              </Box>
              <Box width="30%" height="387px" padding="31px 34px" className="box_active" onMouseOver={this.onBoxhover} onMouseLeave={this.onBoxOut}>
                <Row bottomspacing="41px">
                  <img className="img_right_space" src={require('../../assets/info_box.svg')} alt="search"/>
                  <p>TIPS FOR BUYING A CAR</p>
                </Row>
                <Row bottomspacing="30px">
                  <p className="paragraph_spacing">is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's </p>
                </Row>
                <Row justify="flex-end">
                  <p className="img_right_space linkdefault" >See Tips Now</p>
                  <img src={require(`../../assets/${this.state.box_arrow}.svg`)} alt="arrow_right"/>
                </Row>
              </Box>
            </Row>
            <Row padding="0 91px" bottomspacing="48px">
              <Box width="100%" height="400px" padding="31px 34px">
                <Row justify="space-between">
                  <Col width="38%">
                    <Image src={require('../../assets/valuation.svg')} alt="lady_car"/>
                  </Col>
                  <Col width="58%">
                    <Column align="flex-end">
                      <h1 className="bigheader">Get a free valuation today!</h1>
                      <p className="text_box_right">is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's </p>
                      <Row justify="flex-end">
                        <p className="img_right_space linkdefault">Get Valuation today</p>
                        <img src={require(`../../assets/arrow_right.svg`)} alt="arrow_right"/>
                      </Row>
                    </Column>
                  </Col>
                </Row>
              </Box>
            </Row>
            <Row padding="0 91px" >
              <Box width="100%" height="100%" padding="34px 0px 34px 61px" bottomspacing="5px">

                <Row bottomspacing="40px">
                  <h1>Latest from Auto Trader</h1>
                </Row>
                
                <Row justify="flex-start" height="312.986px">
                  {
                    content
                  }
                </Row>

                <Row justify="flex-end" padding="0 50px 0 0">
                    <p className="img_right_space" onClick={() => {this.props.history.push("/cars")}} style={{cursor: "pointer"}}>See all</p>
                    <img src={require(`../../assets/${this.state.box_arrow}.svg`)} alt="arrow_right"/>
                </Row>
              </Box>             
            </Row>

            <Footer />
          </Container>
        )
    }
}

const mapStateToProps = state => {
  return {
    payload: state.Viewallcars.cars_payload,
    loading: state.Viewallcars.get_cars_loading,
    success: state.Viewallcars.get_cars_successful,
  }
}

export default connect(mapStateToProps, {ViewAll})(Home);
